import styled from 'styled-components'
import Input from './Input'
import Footer from './Footer'
const WrapperForm = styled.div`
  max-width: 645px;
  margin: 60px auto;
  background-color: #fff;
  border-top: solid 6px #fad312;
  box-shadow: 1.8px 2.4px 5px 0 rgba(0, 0, 0, 0.3);
  padding: 30px 40px;
`
const HeaderTitle = styled.h1`
  margin-bottom: 2rem;
  font-weight: bold;
`
const Description = styled.div`
`
const DescInfo = styled.p`
  font-size:0.9em;
  color:black;
  line-height:1em;
`
const RequiredPoint = styled.span`
  color: #e74149; 
`
const Button = styled.button`
  margin-top: 2em;
  padding:0.8em 1.3em;
  border-radius: 3px;
  border:none;
  background-color: #fad312;
  cursor: pointer;
`

function Form({ formTitle, questions,inputValue, handleInputChange, handleCheckInputValue}) {
 
  return (
    <>
      <WrapperForm>
        <HeaderTitle > {formTitle} </HeaderTitle>
        <Description>
          <DescInfo>活動日期：2020/12/10 ~ 2020/12/11</DescInfo>
          <DescInfo>活動地點：台北市大安區新生南路二段1號</DescInfo>
          <RequiredPoint>* 必填</RequiredPoint>
        </Description>
        <form >
          {questions.map((question, index) =>
            <Input
              key={question.title + index}
              inputTitle={question.title}
              inputType={question.type}
              inputValue={question.value|| inputValue[question.title]||''}
              options={question.options}
              required={question.required}
              isValid={question.isValid}
              placeholder={question.placeholder}
              desc={question.desc}
              handleInputChange={handleInputChange}
               />
          )}
          <Button onClick={handleCheckInputValue}>提交</Button >
          <DescInfo>請勿透過表單送出您的密碼。</DescInfo>
        </form>
      </WrapperForm>
      <Footer />
    </>
  );
}

export default Form;
