
import './App.css';
import Form from './components/Form'
import React, { useState } from 'react';

const questionArray = [
  {
    title: '暱稱',
    type: 'text',
    desc: null,
    required: true,
    options: null,
    value: '',
    placeholder: '請輸入你的暱稱',
    isValid: false,
  },
  {
    title: '電子郵件',
    type: 'email',
    desc: null,
    required: true,
    options: null,
    value: '',
    placeholder: '請輸入你的電子郵件',
    isValid: false,
  },
  {
    title: '手機號碼',
    type: 'tel',
    desc: null,
    required: true,
    value: '',
    options: null,
    placeholder: '請輸入你的手機號碼',
    isValid: false,
  },
  {
    title: '報名類型',
    type: 'radio',
    desc: null,
    required: true,
    value: '',
    options: [{ id: 1, content: '躺在床上用想像力實作' }, { id: 2, content: '趴在地上滑手機找現成的' }],
    placeholder: '請選擇你的報名類型',
    isValid: false,
  },
  {
    title: '怎麼知道這個活動的？',
    type: 'text',
    desc: null,
    required: true,
    value: '',
    options: null,
    placeholder: '請輸入你的回答',
    isValid: false,
  }, {
    title: '其他',
    type: 'text',
    desc: '對活動的一些建議',
    required: false,
    value: '',
    options: null,
    placeholder: '請輸入你的回答',
    isValid: false,
  },


]
function checkValid(data) {
  let type = data.type
  let value = data.value
  let emailRule = /^\w+((-\w+)|(\.\w+))*\@[A-Za-z0-9]+((\.|-)[A-Za-z0-9]+)*\.[A-Za-z]+$/
  let phoneRule = /^09\d{8}/
  if (type === 'email') return emailRule.test(value)
  if (type === 'tel') return phoneRule.test(value)
  return true
}

function App() {
  const formTitle = '新拖延運動報名表單';
  const isValid = false;
  let hasError = false;
  let answers = {}
  const [questions, setQuestions] = useState(questionArray)
  const [values, setValue] = useState({ name: '' })
  const handleInputChange = (e) => {
    const target = e.target
    const value = target.value;
    const name = target.name;
    const newQuestions = questions.map(question => {
      if (question.title !== name) return question
      return {
        ...question,
        value,
        isValid
      }
    })
    setValue({ [name]: value });
    setQuestions(newQuestions)
  }
  const handleCheckInputValue = (e) => {
    e.preventDefault();
    const filterIsValid = questions.map(question => {
      return !question.required ? question :
        question.value.length === 0 ? { ...question, isValid: true } :
          checkValid(question) ? { ...question, isValid } : { ...question, isValid: true }
    })
    setQuestions(filterIsValid)
    const checkError = filterIsValid.find(item => item['isValid'] === true)
    if (typeof checkError !== 'undefined') {
      hasError = true
      console.log('我是進入錯誤區')
    }
    if (!hasError) {
      console.log('我是進入alert')
      for (let i = 0; i < questions.length; i += 1) {
        answers[questions[i].title] = questions[i].value
      }
      alert(JSON.stringify(answers))
    }
  }
  return (
    <Form
      formTitle={formTitle}
      questions={questions}
      inputValue={values}
      handleInputChange={handleInputChange}
      handleCheckInputValue={handleCheckInputValue}
    />

  );
}

export default App;
